import React,{useState , useEffect} from 'react';


function Covid(){

    const [rec , setRec] =useState([]);

    useEffect(() =>{
        getRecord()
    },[])

    const getRecord = () =>{
        let url="https://data.covid19india.org/data.json";
        fetch(url)
        .then(res => res.json())
        .then(data => {
            console.log(data.statewise[0]);
            setRec(data.statewise[0]);
        })
    }

    return(
        <>
        <h1 className="heading">Covid-19 CoronaVirus Tracker</h1>
        <h2 className="heading1">Live Results</h2>
        <div id="container">
          <div id="info">
             <p>Country  </p>
             <h3>India</h3>
          </div>
          <div id="info">
             <p>Recovered  </p>
             <h3>{rec.recovered}</h3>
          </div>
          <div id="info">
             <p>Confirmed  </p>
             <h3>{rec.confirmed}</h3>
          </div>
          <div id="info">
             <p>Death  </p>
             <h3>{rec.deaths}</h3>
          </div>
          <div id="info">
             <p>Active  </p>
             <h3>{rec.active}</h3>
          </div>
        </div>
        </>
    )
}

export default Covid;